import Home from '../components/Home.vue'
import Movie from '../components/Movie.vue'
import Books from '../components/Books.vue'
import Broadcast from '../components/Broadcast.vue'
import Group from '../components/Group.vue'
export default[
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path:'/movie',
      component:Movie
    },
    {
      path:'/books',
      component:Books
    },
    {
      path:'/broadcast',
      component:Broadcast
    },
    {
      path:'/group',
      component:Group
    },
    {
      path:'/',
      component:Home
    },
    {
      path:'*',
      component:Home
    }
  ]



