import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import Router from './router/index'
Vue.use(VueRouter);
const router = new VueRouter({
  mode:'history',
  routes:Router
})

// Vue.config.productionTip = false
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



